<template>
  <div class="home">
    <div v-if="authHeader === null">
      <input type="text" v-model="key" placeholder="access code"/>
      <button @click="submit">Enter Code</button>
    </div>
    <div v-else>
      <p>You have entered a code, if you ended up here you need to re-enter the code. Hit refresh to start over.</p>
    </div>
    {{coords}}<br/><br/>
    {{location}}
  </div>
</template>

<script>
import { api } from '@/utils/api'
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'Home',
  data () {
    return {
      key: null,
      coords: null,
      location: null
    }
  },
  mounted() {
    this.$getLocation().then(coordinates => {
      this.coords = coordinates
      let pd = {
        "location": {
          "latLng": {
            "lat": coordinates.lat,
            "lng": coordinates.lng
          }
        },
        "options": {
          "thumbMaps": false
        },
        "includeNearestIntersection": true,
        "includeRoadMetadata": true
      }
      this.getLocationFull(pd)
    })
  },
  methods: {
    ...mapActions([
      'setAuthHeader'
    ]),
    getLocationFull (data) {
      api.rawPostSpec('https://open.mapquestapi.com/geocoding/v1/reverse?key=CCXgK2nYZAfH4lv4AGC6MchxBESEka2A', data).then( response => {
        this.location = response.data
      }).catch( err => {
        alert(err)
      })
    },
    submit () {
      this.setAuthHeader(this.key)
    }
  },
  computed: {
    ...mapGetters([
      'authHeader'
    ])
  }
}
</script>
