<template>
  <div>
    <p>{{center.name}} - <button @click="deleteRecord(center._id)">delete</button> - {{center.centerId}} - <button @click="editMode">Edit entry</button></p>
    <div v-if="showMore">
      <input type="text" v-model="center.name" placeholder="name" /><br />
      <input type="text" v-model="center.address" placeholder="address" /><br />
      <input type="text" v-model="center.city" placeholder="city" /><br />
      <input type="text" v-model="center.state" placeholder="state" /><br />
      <input type="text" v-model="center.zip" placeholder="zip" /><br />
      <input type="text" v-model="center.url" placeholder="url" /><br />
      <input type="text" v-model="center.hours" placeholder="hours" /><br />
      <input type="text" v-model="center.notes" placeholder="notes" /><br />
      <button @click="updateRecord">Update entry</button>
    </div>
  </div>
</template>

<script>
import  { mapGetters } from 'vuex'
import { api } from '@/utils/api'
export default {
  name: 'CenterItem',
  computed: {
    ...mapGetters([
      'authHeader'
    ])
  },
  data () {
    return {
      showMore: false
    }
  },
  props: [
    'center'
  ],
  methods: {
   deleteRecord(id) {
      if(!this.authHeader)
        this.setAuthHeader(this.token)
      api.deletedata('/cov/center', id, {token: this.authHeader} ).then( response => {
        if(response.data.errors) {
          alert('problems')
        } else {
          this.$emit('reload')
        }
      }).catch( err => {
        alert(err + ' post')
      })
    },
    editMode () {
      this.showMore = true
    },
    updateRecord () {
      api.putData('/cov/center', this.center, {token: this.authHeader} ).then( response => {
        if(response.data.errors) {
          alert('problems')
        } else {
          this.showMore = false
        }
      }).catch( err => {
        alert(err + ' post')
      })
    }
  }
}
</script>

