const state = {
  authHeader: null
}

const mutations = {
  mutateAuthHeader (state, token) {
    state.authHeader = token
  }
}

const actions = {
  setAuthHeader ({commit}, _token) {
    // storage.storeValue('token', _token)
    commit('mutateAuthHeader', _token) 
  }
}

const getters = {
  authHeader: state => {
    /*let header 
    if(state.authHeader) {
      header = state.authHeader
    } else {
      if(storage.getValue('token')) {
        header = storage.getValue('token').toString()
      }
    }*/
    return state.authHeader
  }
}

export default {
  state,
  actions,
  mutations,
  getters
}