<template>
  <div>
    <p>{{restaurant.name}} - <button @click="deleteRecord(restaurant._id)">delete</button> - {{restaurant.restId}} - <button @click="editMode">Edit entry</button></p>
    <div v-if="showMore">
      <input type="text" v-model="restaurant.name" placeholder="name" /><br />
      <input type="text" v-model="restaurant.address" placeholder="address" /><br />
      <input type="text" v-model="restaurant.city" placeholder="city" /><br />
      <input type="text" v-model="restaurant.state" placeholder="state" /><br />
      <input type="text" v-model="restaurant.zip" placeholder="zip" /><br />
      <input type="text" v-model="restaurant.url" placeholder="url" /><br />
      <input type="text" v-model="restaurant.hours" placeholder="hours" /><br />
      <input type="text" v-model="restaurant.notes" placeholder="notes" /><br />
      <button @click="updateRecord">Update entry</button>
    </div>
  </div>
</template>

<script>
import  { mapGetters } from 'vuex'
import { api } from '@/utils/api'

export default {
  name: 'RestaurantItem',
  computed: {
    ...mapGetters([
      'authHeader'
    ])
  },
  data () {
    return {
      showMore: false
    }
  },
  props: [
    'restaurant'
  ],
  methods: {
   deleteRecord(id) {
      if(!this.authHeader)
        this.setAuthHeader(this.token)
      api.deletedata('/cov/rest', id, {token: this.authHeader} ).then( response => {
        if(response.data.errors) {
          alert('problems')
        } else {
          this.$emit('reload')
        }
      }).catch( err => {
        alert(err + ' post')
      })
    },
    editMode () {
      this.showMore = true
    },
    updateRecord () {
      api.putData('/cov/rest', this.restaurant, {token: this.authHeader} ).then( response => {
        if(response.data.errors) {
          alert('problems')
        } else {
          this.showMore = false
        }
      }).catch( err => {
        alert(err + ' post')
      })
    }
  }
}
</script>

