import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/restaurants',
    name: 'Restaurants',
    component: () => import(/* webpackChunkName: "about" */ '../views/Restaurants.vue')
  },
  {
    path: '/centers',
    name: 'Centers',
    component: () => import(/* webpackChunkName: "about" */ '../views/Centers.vue')
  },
  {
    path: '/signups',
    name: 'Signups',
    component: () => import(/* webpackChunkName: "about" */ '../views/Signups.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
